<template>
  <div
    class="page-about"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-about__root">
      <div id="page-about__wrapp" ref="contentWrap" class="page-post__wrapp">
        <the-header-new />

        <div class="page-about__main">

        <div class="page-about__main-wrapper">
          <div class="page-about__main-content">

            <div class="page-about__main-sidebar">
              <div class="page-about__sidebar-post">
                <router-link :to="{ name: 'aboutus' }" class="page-about__sidebar-post-title">About us</router-link>
              </div>
              <div class="page-about__sidebar-post">
                <router-link :to="{ name: 'aboutethermium' }" class="page-about__sidebar-post-title">What is DMEX?</router-link>
              </div>
              <div class="page-about__sidebar-post">
                <router-link :to="{ name: 'aboutdecenralization' }" :class="{ 'router-link-exact-active': ['aboutdecenralization'].includes($route.name) }" class="page-about__sidebar-post-title">What is Decentralization?</router-link>
              </div>
              <div class="page-about__sidebar-post">
                <router-link :to="{ name: 'support' }" class="page-about__sidebar-post-title">Contact us</router-link>
              </div>
              <div class="page-fees__sidebar-post">
                <router-link :to="{ name: 'fees' }" class="page-fees__sidebar-post-title">Fees</router-link>
              </div>
            </div>

            <div class="page-about__main-article">

              <div class="page-about__heading">
                <div @click="$router.go(-1)" class="page-about__back"></div>
                <div class="page-about__heading-main">What is Decentralization?</div>
                <div class="page-about__heading-description">Decentralization is the process of removing the need for a centralized authority to intermediate transactions between individuals.</div>
              </div>

              <div class="page-about__text">
                <p>Most of the current information systems are working in a centralized structure. Be it Facebook, Amazon or your Bank, all of your operations go through a network of humans and computers that have the power to block access, freeze your funds or delay your transactions. This control makes corporations vulnerable to pressure from governments to exercise those powers. Moreover, those systems require you to trust the company or the bank that they indeed store your information and funds properly. To ensure that, complex systems of officers and auditors must perform regular checks, ultimately the cost of this checks is passed to the customer, adding unnecessary costs.</p>
                <p>In the decentralized world, there are no central authorities exercising control over how your operations are executed, removing frictions, unnecessary delays and costs.</p>
                <p>Since 2009, Bitcoin has proven to be the first ever censorship-resistant financial system, not capable of being controlled by any single party. Ethereum introduces the next generation of blockchain applications - smart contracts. A smart contract is best described as a programmed bank account that executes operations according to some strict pre-programmed rules. Smart contracts allow for the creation of Decentralized Applications (DAPPs) that can run autonomously on the Ethereum blockchain.</p>
                <p>A smart contract resides on the blockchain and once deployed can never be stopped or interfered with (unless programmed otherwise). The structure of the blockchain guarantees this. The blockchain is a distributed transaction ledger that is replicated on thousands of computers around the globe, called nodes. To amend a transaction or a contract, all nodes would have to be amended at the same time, which is impossible in practice.</p>
                <p>Smart contracts mark a new era of cooperation between individuals. An era, where trust into third parties is no longer required, were individuals can interact between each over without interference. The future belongs to decentralized applications with no borders and no jurisdictions.</p>
              </div>
            </div>
          </div>
        </div>
      </div>

        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Страница поста
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';

import iconArrow from '@/icons/e-icon_arrow.svg';
import iconBack from '@/icons/e-icon__back-big.svg';

// Components
import TheTokenSearchEtherscanMobile from '@/containers/blocks/TheTokenSearchEtherscanMobile';
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMainInfo from '@/containers/blocks/TheMainInfo';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMobileTokenSearch from '@/containers/blocks/TheMobileTokenSearch';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';


export default {
  name: 'page-aboutdecenralization',

  data() {
    return {
      iconArrow,
      iconBack,
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheTokenSearchEtherscanMobile,
    TheMobileTokenSearch,
    TheTokenSearchResultNew,
    EBigNumber,
    EButton,
    EIcon,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMainInfo,
    TheMenuSidebar,
    ThePopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-about {
  $this: &;

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: scroll;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #031472;
    position: relative;
    margin-top: -120px;
    padding-top: 120px;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      bottom: -120px;
      width: 100%;
      max-width: 1600px;
      height: 180px;
      background: #030C52;
      border-radius: 50%;
      filter: blur(30px);
      opacity: .4;

      @include r(992) {
        display: none;
      }
    }
  }

  &__main-wrapper {
    background: #f2f2f2;
    z-index: 1;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 130px 190px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;

    @include r(1200) {
      padding: 70px 90px 150px;
    }

    @include r(992) {
      position: relative;
      padding: 50px 40px;
      flex-direction: column-reverse;
      justify-content: flex-start;
    }
  }

  &__main-article {
    width: 75%;
    margin-right: 120px;
    position: relative;

    @include r(1220) {
      margin-right: 60px;
    }

    @include r(992) {
      width: 100%;
    }

    @include r(420) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-weight: normal;
    margin-bottom: 32px;
    position: relative;

    @include r(640) {
      margin-bottom: 26px;
    }
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 16px;

    @include r(768) {
      left: -32px;
    }

    @include r(640) {
      top: 11px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__heading-main {
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__heading-description {
    color: #000;
    font-size: 23px;
    line-height: 36px;
  }

  &__text {
    p {
      color: #000;
      opacity: 0.6;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
    }
  }

  &__main-sidebar {
    padding-top: 19px;
    width: 25%;

    @include r(992) {
      position: static;
      transform: translateX(0);
      box-shadow: none;
      width: 100%;
      padding-top: 70px 0 0;
      background: transparent;
      height: auto;
    }
  }

  &__sidebar-post {
    margin-bottom: 16px;
  }

  &__sidebar-post-title {
    font-size: 16px;
    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    margin-top: 5px;
    text-decoration: none;

    &.router-link-exact-active {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }
}

</style>



